body {
  --fadedBlueTextboxColor: #707793;
  --redColor: #AC3B61;
  --redColorButtonFaded: #AC3B61A0;
  --redColorButtonFadedShadow: #AC3B6155;
  --fadedBlueTextboxShadow: #707793A0;
  background-color: #2E3047;
  margin: 0;
  max-width: 100%;
  overflow-x: hidden;
}

h1 {
  font-size: x-large;
}

h2 {
  font-size: small;
}

p {
  font-size: 12px;
}

h1, h2, p, li, td, text {
  color: #EEEEEE;
  font-family: Arial, Helvetica, sans-serif;
}

button {
  background-color: var(--redColorButtonFaded);
  color: #EEEEEE;
  border: none;
  font-size: small;
  margin-top: 20px;
  border-radius: 10px;
  padding: 10px;
  box-shadow: -3px 4px var(--redColorButtonFadedShadow);
  margin-left: 3px;
}

button:active {
  box-shadow: none;
  transform: translate(-3px, 4px)
}

td {
  border: solid 1px black;
  border-collapse: collapse
}

input {
  background-color:var(--fadedBlueTextboxColor);
  border: none;
  height: 30px;
  padding-left: 15px;
  color: #EEEEEE;
  width: 120px;
  margin-bottom: 20px;
}

input::placeholder {
  color: #EEEEEE;
  opacity: .5;
}

input:disabled {
  background-color: #989ca9;
}

input:focus {
  border: none;
  outline: none;
  box-shadow: -2px 2px var(--fadedBlueTextboxShadow);
}

table, th, td {
  border: 2px solid white;
  border-collapse: collapse;
  padding: 8px;
}

table {
  table-layout:fixed;
  width: 100%;
}

#loading-icon {
  color: #EEEEEE;
  margin: 50px;
}